"use client";

import { useState } from "react";
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
} from "@/components/ui/dialog";
import { Card, CardContent } from "@/components/ui/card";
import { ChevronRight } from "lucide-react";

export default function CustomerShopsTable({ shops }) {
    const [selectedShop, setSelectedShop] = useState(null);

    console.log(shops)

    if (!shops || shops.length === 0) {
        return (
            <Card className="rounded-2xl">
                <CardContent className="p-6 text-center text-sm text-muted-foreground">
                    No shop found.
                </CardContent>
            </Card>
        );
    }

    return (
        <>
            {/* Shop List */}
            <div className="space-y-3">
                {shops.map((item) => {
                    const percentage = Math.round(
                        (item.stampCount / item.shop.targetStamps) * 100
                    );

                    return (
                        <Card
                            key={item.id}
                            onClick={() => setSelectedShop(item)}
                            className="cursor-pointer active:scale-[0.98] transition-all duration-150 rounded-2xl"
                        >
                            <CardContent className="p-4">
                                <div className="flex justify-between items-center">
                                    <div>
                                        <h2 className="font-semibold">
                                            {item.shop.shopName}
                                        </h2>
                                        <p className="text-sm text-muted-foreground mt-1">
                                            {item.stampCount} / {item.shop.targetStamps} stamps
                                        </p>
                                    </div>

                                    <div className="flex items-center gap-2">
                                        <span className="text-sm font-medium">
                                            {percentage}%
                                        </span>
                                        <ChevronRight className="size-4 text-muted-foreground" />
                                    </div>
                                </div>

                                {/* Progress Bar */}
                                <div className="mt-3 h-2 bg-muted rounded-full overflow-hidden">
                                    <div
                                        className="h-full bg-primary rounded-full transition-all duration-500"
                                        style={{ width: `${percentage}%` }}
                                    />
                                </div>
                            </CardContent>
                        </Card>
                    );
                })}
            </div>

            {/* Dialog with Detailed Card */}
            <Dialog
                open={!!selectedShop}
                onOpenChange={() => setSelectedShop(null)}
            >
                {selectedShop && (
                    <DialogContent className="sm:max-w-md rounded-3xl">
                        <DialogHeader>
                            <DialogTitle>
                                {selectedShop.shop.shopName}
                            </DialogTitle>
                        </DialogHeader>

                        <Card className="border-none shadow-none">
                            <CardContent className="space-y-4 p-0">

                                <div className="text-sm">
                                    <span className="font-medium">Collected Stamps:</span>{" "}
                                    {selectedShop.currentStamps} / {selectedShop.totalStamps}
                                </div>

                                <div>
                                    <div className="h-3 bg-muted rounded-full overflow-hidden">
                                        <div
                                            className="h-full bg-primary rounded-full transition-all duration-500"
                                            style={{
                                                width: `${Math.round(
                                                    (selectedShop.currentStamps /
                                                        selectedShop.totalStamps) *
                                                    100
                                                )}%`,
                                            }}
                                        />
                                    </div>
                                </div>

                                <div className="text-sm text-muted-foreground">
                                    Reward: {selectedShop.reward || "Free Item"}
                                </div>

                                {selectedShop.currentStamps >=
                                    selectedShop.totalStamps && (
                                        <div className="text-green-600 text-sm font-medium">
                                            ðŸŽ‰ Reward Unlocked!
                                        </div>
                                    )}
                            </CardContent>
                        </Card>
                    </DialogContent>
                )}
            </Dialog>
        </>
    );
}
